import { Injectable } from '@angular/core';
import { Observable, Observer } from 'rxjs';

import { Options } from './options';
import { Type } from './type';

import * as webNotification from 'simple-web-notification';

@Injectable({
  providedIn: 'root'
})
export class NgxPushNotificationService {
  showNotification(options: Options): Observable<Type> {
    const observable = new Observable((observer: Observer<Type>) => {
      webNotification.showNotification(options.title, {
        body: options.body,
        icon: options.icon,
        onClick: function onNotificationClicked() {
          observer.next({
            type: 'click'
          });
        },
        autoClose: 4000
      }, function onShow(error, hide) {
        if (error) {
          observer.error(error.message);
        } else {
          observer.next({
            type: 'show'
          });

          setTimeout(function hideNotification() {
            observer.next({
              type: 'close'
            });
            hide();
          }, 4000);
        }
      });
    });

    return observable;
  }
}
